<template>
  <div id="app">
    <el-button type="primary" @click="addDb">添加连接</el-button>
    <el-table :data="dbList" style="width: 100%">
      <el-table-column prop="usersId" label="ID"></el-table-column>
      <el-table-column prop="usersUrl" label="url"></el-table-column>
      <el-table-column prop="usersPort" label="端口"></el-table-column>
      <el-table-column prop="usersUid" label="用户名"></el-table-column>
      <el-table-column prop="usersDb" label="数据库"></el-table-column>
      <el-table-column prop="usersDriver" label="jdbc驱动"></el-table-column>
      <el-table-column fixed="right" label="操作" width="100">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small">生成</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="添加连接" :visible.sync="addDbDialogVisible" width="30%">
      <el-form ref="formAddDb" :model="formAddDb" label-width="120px">
        <el-form-item label="url" prop="usersUrl">
          <el-input v-model="formAddDb.usersUrl"></el-input>
        </el-form-item>
        <el-form-item label="端口" prop="usersPort">
          <el-input v-model="formAddDb.usersPort"></el-input>
        </el-form-item>
        <el-form-item label="用户名" prop="usersUid">
          <el-input v-model="formAddDb.usersUid"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="usersPwd">
          <el-input v-model="formAddDb.usersPwd"></el-input>
        </el-form-item>
        <el-form-item label="数据库" prop="usersDb">
          <el-input v-model="formAddDb.usersDb"></el-input>
        </el-form-item>
        <el-form-item label="数据库类型" prop="dbType">
          <el-select v-model="formAddDb.dbType" placeholder="请选择">
            <el-option v-for="item in dbTypeOptions" :key="item.value" :label="item.label"
              :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmitAddDb">添 加</el-button>
          <el-button @click="handleCloseAddDb">取 消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <el-dialog title="生成" :visible.sync="dialogVisible" width="30%">
      <el-form ref="form" :model="form" label-width="120px">
        <el-form-item label="ID" prop="id">
          <el-input v-model="form.id" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="表名" prop="tableName">
          <el-input v-model="form.tableName"></el-input>
        </el-form-item>
        <el-form-item label="使用Lombok" prop="useLombok">
          <el-radio-group v-model="form.useLombok">
            <el-radio label="1">是</el-radio>
            <el-radio label="0">否</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="添加注释" prop="useComment">
          <el-radio-group v-model="form.useComment">
            <el-radio label="1">是</el-radio>
            <el-radio label="0">否</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">生 成</el-button>
          <el-button @click="handleClose">取 消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      prefix: '',
      // prefix: 'http://localhost:8080',
      dbList: [],
      dialogVisible: false,
      addDbDialogVisible: false,
      currentRow: null,
      form: {
        id: '',
        tableName: '',
        useLombok: '1',
        useComment: '1'
      },
      formAddDb: {
        usersUrl: '',
        usersPort: '',
        usersUid: '',
        usersPwd: '',
        usersDb: '',
        dbType: undefined
      },
      dbTypeOptions: [
        {
          value: 1,
          label: 'Oracle'
        },
        {
          value: 2,
          label: 'MySql'
        },
        {
          value: 3,
          label: 'MariaDB'
        },
      ]
    }
  },
  mounted() {
    axios.get(this.prefix + '/main/list', {
      params: {
      }
    }).then((res) => {
      console.log(res);
      if (res.data.code === 200) {
        this.dbList = res.data.data
      }
    })
  },
  methods: {
    convertBool(v) {
      return v === '1'
    },
    handleClick(row) {
      this.form.id = row['usersId']
      this.dialogVisible = true
    },
    handleClose() {
      this.$refs['form'].resetFields()
      this.dialogVisible = false
    },
    onSubmit() {
      let param = Object.assign({}, this.form)
      param['useLombok'] = this.convertBool(param['useLombok'])
      param['useComment'] = this.convertBool(param['useComment'])
      axios.post(this.prefix + '/main/gen', param).then((res) => {
        console.log(res);
        if (res.data.code === 200) {
          this.$message({
            message: '成功',
            type: 'success'
          })
        } else {
          this.$message({
            message: res.data.msg,
            type: 'warning'
          })
        }
      })
    },
    addDb() {
      this.addDbDialogVisible = true
    },
    onSubmitAddDb() {
      axios.post(this.prefix + '/main/add', this.formAddDb).then((res) => {
        console.log(res);
        if (res.data.code === 200) {
          this.$message({
            message: '成功',
            type: 'success'
          })
        } else {
          this.$message({
            message: res.data.msg,
            type: 'warning'
          })
        }
      })
    },
    handleCloseAddDb() {
      this.$refs['formAddDb'].resetFields()
      this.addDbDialogVisible = false
    }
  }
}
</script>

<style></style>



